html{margin:0;padding:0}body{text-align:center;background:#141219;color:gray;font-family:Ubuntu,sans-serif}header{position:relative;display:block;height:90px}header .logo{position:absolute;top:50%;left:15px;-webkit-transform:translateY(-50%);-moz-transform:translateY(-50%);transform:translateY(-50%)}header .logo :hover{filter:brightness(1.1)}header .nav{position:absolute;top:50%;right:15px;-webkit-transform:translateY(-50%);-moz-transform:translateY(-50%);transform:translateY(-50%)}header .nav ul li{list-style-type:none;display:inline-block;margin-right:10px}header .nav ul li.last{margin:0}header .nav ul li a{line-height:normal}header nav,header nav ul,header nav ul li{margin:0;padding:0}header .navImg{max-width:30px}.mainComponent .commandLine{box-sizing:border-box;padding-bottom:13px;border-bottom:1.5px solid #fff;border-radius:4px}.mainComponent .commandLine .textBox{background-color:#433453;color:#fff;border:none;width:200px;padding:0 5px}@media (min-width:350px) and (max-width:650px){.mainComponent .commandLine .textBox{width:250px}}@media (min-width:650px) and (max-width:850px){.mainComponent .commandLine .textBox{width:400px}}@media (min-width:850px){.mainComponent .commandLine .textBox{width:500px}}.mainComponent .commandLine .textBox:focus{background-color:rgba(67,52,83,.5);color:#f5f5f5}.mainComponent .commandLine .textEnter{padding:2px 20px;height:35px;background-color:#fff;border:none;border-radius:2px;color:#000;cursor:pointer}.mainComponent .commandResult{margin-top:20px;margin-left:.5;margin-right:.5;word-break:break-all}.about #welcome{height:85vh;display:flex;flex-direction:column;align-items:center;justify-content:center}@media (min-width:850px){.about #welcome{flex-direction:row}}.about #welcome #name{font-family:Syncopate,Haettenschweiler,'Arial Narrow Bold',sans-serif;font-size:40px;font-weight:700;margin:10px}@media (min-width:850px){.about #welcome #name{margin:100px}}.about #welcome #royalName{font-family:Syncopate,Haettenschweiler,'Arial Narrow Bold',sans-serif;font-size:64px;font-weight:700;color:#fff;position:absolute;top:65%;left:50%;transform:translate(-50%,-50%)}.about #welcome #crown{top:0;bottom:500px;height:30vh}@media (min-width:850px){.about #welcome #crown{height:50vh}}.about #interests{border:2px red solid;height:85vh}.about #projects{border:2px green solid;height:85vh}.about #timelime{border:2px #00f solid;height:85vh}footer{position:fixed;left:0;bottom:0;width:100%;font-size:12px}footer button{background-color:transparent;border:none;color:gray;font-size:12px;cursor:pointer;-webkit-transition:color .4s ease;-moz-transition:color .4s ease;-ms-transition:color .4s ease;-o-transition:color .4s ease;transition:color .4s ease}footer button:hover{color:#9400c5}